'use strict';

// MODULES //

import isFloat32Array from '@stdlib/assert-is-float32array';
import hasAlmostSameValues from '@stdlib/array-base-assert-has-almost-same-values';


// MAIN //

/**
* Tests if two arguments are both Float32Arrays and contain respective elements which are approximately the same value within a specified number of ULPs (units in the last place).
*
* ## Notes
*
* -   In contrast to the strict equality operator `===`, `-0` and `+0` are distinguishable and `NaNs` are the same.
*
* @param {*} v1 - first value
* @param {*} v2 - second value
* @param {NonNegativeInteger} maxULP - maximum allowed ULP difference
* @returns {boolean} boolean result
*
* @example
* import Float32Array from '@stdlib/array-float32';
*
* var x = new Float32Array( [ 1.0, 2.0, 3.0 ] );
* var y = new Float32Array( [ 1.0, 2.0, 3.0 ] );
*
* var out = isAlmostSameValueFloat32Array( x, y, 0 );
* // returns true
*
* @example
* import Float32Array from '@stdlib/array-float32';
*
* var x = new Float32Array( [ 1.0, 2.0, 3.0 ] );
* var y = new Float32Array( [ 1.0, 2.0, 4.0 ] );
*
* var out = isAlmostSameValueFloat32Array( x, y, 1 );
* // returns false
*/
function isAlmostSameValueFloat32Array( v1, v2, maxULP ) { // eslint-disable-line id-length
	return ( isFloat32Array( v1 ) && isFloat32Array( v2 ) && hasAlmostSameValues( v1, v2, maxULP ) );  // eslint-disable-line max-len
}


// EXPORTS //

export default isAlmostSameValueFloat32Array;
